@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.about-container{width:70vw;height:auto;display:flex;flex-direction:column;align-items:center;justify-content:center;margin:0 auto}.about-container h2{width:100%;text-align:left;margin-top:3rem}

.main-container{width:80%;height:auto;display:flex;flex-direction:column;align-items:center;text-align:center;border-radius:.5rem;margin-top:-3rem;z-index:1;background-color:white;margin-left:auto;margin-right:auto}.main-container .bamboo-img{margin-top:-1.5rem}.main-container .text-content{padding:2rem 1rem 0 1rem}.main-container .button-container{width:100%;display:flex;align-items:center;justify-content:space-evenly;margin-top:3rem}.main-container .button-container .bookmark,.main-container .button-container .black-bookmark.active{display:none}.main-container .button-container .bookmark.active{display:flex;align-items:center}.main-container .button-container .bookmark.active img{margin-right:0.5rem;color:#3cb4ac}.main-container .button-container .bookmark.active span{font-size:.7rem}.main-container .button-container .btn{background-color:#3cb4ac;padding:1.1rem 1.5rem;border-radius:2rem;color:white;border:none;cursor:pointer}.main-container .button-container .btn:active,.main-container .button-container .btn:focus{outline:none}.main-container .button-container .btn:hover{background-color:#147b74}.main-container .amount-container{width:80%;height:auto;display:flex;flex-direction:column;margin-top:3rem}.main-container .amount-container p{border-bottom:1px solid lightgrey;width:30%;padding-bottom:2rem;margin:0 auto}.main-container .amount-container .border-none{border-bottom:none}.main-container .amount-container .sideBar{display:flex;flex-direction:column;justify-content:flex-start;width:80%;text-align:left;border-top:10px solid #3cb4ac;border-radius:1rem}

.header{width:100vw;height:auto;display:flex;position:relative}.header .mobile-img{z-index:-1}.header .desktop-img{display:none}.header .menu{display:flex;width:100%;height:auto;align-items:center;justify-content:space-between;padding:2rem 1rem;position:absolute;top:0}.header .menu .hamburger,.header .menu .close.active{display:flex;width:10%}.header .menu .nav-menu,.header .menu .hamburger.active,.header .menu .close,.header .menu .navbar-list{display:none}.header .nav-menu.active{width:100vw;height:auto;display:flex;flex-direction:column;align-items:center;justify-content:center;position:absolute;bottom:0}.header .nav-menu.active .nav-list{list-style:none;display:flex;flex-direction:column;align-items:center;width:71%;border-radius:.5rem;background-color:white}.header .nav-menu.active .nav-list .border{display:flex;margin-left:-2.5rem;width:100%;border-bottom:1px solid #d3cfcf;padding:2rem 0}.header .nav-menu.active .nav-list .border:nth-child(3){border-bottom:0}.header .nav-menu.active .nav-list .nav-item{color:#000;text-decoration:none;margin-left:1rem}.header .navbar{width:100vw;height:auto;display:none;align-items:center;justify-content:space-between;padding:1rem .5rem}

.stand-container{width:100vw;height:auto;display:flex;flex-direction:column;align-items:center}.stand-container .stand-content{align-items:center;width:70%;height:45vh;padding:2rem;border:1px solid lightgray;border-radius:.5rem;margin:2rem 0}.stand-container .stand-content div{margin-bottom:2rem}.stand-container .stand-content .pledge{color:#3cb4ac}.stand-container .stand-content .stock{font-size:1.8rem;font-weight:bold}.stand-container .stand-content .stand-btn{background-color:#3cb4ac;padding:1.1rem 2rem;border-radius:2rem;color:white;border:none;font-size:1.2rem;cursor:pointer}.stand-container .stand-content .stand-btn:hover{background-color:#147b74}.stand-container .stand-content .stand-btn:active,.stand-container .stand-content .stand-btn:focus{outline:none}

